





























































var utils = require('../../../commen/util.js');
import uniNavBar from '../../../components/uni-nav-bar.vue';
import uniIcon from "../../../components/uni-icon.vue";
import uniSegmentedControl from '../../../components/uni-segmented-control.vue';

export default {
	data() {
		return {
			user: {
				username: "18310189716",
			},
			items: [
				'我的余额',
				'我的收益'
			],
			current: 0,
			activeColor: '#fc3a39',
			styleType: 'button',
			balance: 3.88,
			shows: 1,
			bindingText: '请先绑定银行卡'
		};
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		styleChange(evt) {
			const value = evt.target.value;
			if (this.styleType !== value) {
				this.styleType = value;
				for (let i = 0; i < this.styles.length; i++) {
					if (this.styles[i].value === value) {
						this.styleIndex = i;
						break;
					}
				}
			}
		},
		alipay() {
			this.shows = 1;
			uni.showToast({
				title: '支付宝',
				mask: false,
				duration: 1500,
				icon:'none'
			});
		},
		bankCard() {
			this.shows = 2;
			uni.showToast({
				title: '银行卡',
				mask: false,
				duration: 1500,
				icon:'none'
			});
		}			

	},
	computed: {
		passUserName() {
			return this.$utils.defaultPlusXing(this.user.username);
		}
	},
	components: {
		uniNavBar,
		uniIcon,
		uniSegmentedControl
	}
}
